import { Component, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { QRCodeComponent } from 'angularx-qrcode';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'my-app';
  fileUrl;
  link: string;
  qrdata = 'Your QR code data string';

  public elementType: 'img' | 'url' | 'canvas' | 'svg' = null;
  public level: 'L' | 'M' | 'Q' | 'H';
  public scale: number;
  public width: number;

  constructor(private sanitizer: DomSanitizer, ) {

    this.elementType = 'img';
    this.level = 'M';
    this.qrdata = '';
    this.scale = 1;
    this.width = 360;
  }

  @ViewChild('qrcode', {static : true}) qrcode: QRCodeComponent;

  // tslint:disable-next-line: typedef
  dlDataUrlBin() {
    this.link = this.qrcode.qrcElement.nativeElement.firstChild.src;
  }

  // tslint:disable-next-line: use-lifecycle-interface
  ngOnInit() {
    const blob = new Blob([this.qrdata], { type: 'application/octet-stream' });

    // To download the Qr code's internal text content
    this.fileUrl = this.sanitizer.bypassSecurityTrustResourceUrl(window.URL.createObjectURL(blob));
  }

  // tslint:disable-next-line: typedef
  saveAsImage(parent) {
    // fetches base 64 date from image
    const parentElement = parent.el.nativeElement.querySelector('img').src;

    // converts base 64 encoded image to blobData
    const blobData = this.convertBase64ToBlob(parentElement);

    // saves as image
    if (window.navigator && window.navigator.msSaveOrOpenBlob) { // IE
      window.navigator.msSaveOrOpenBlob(blobData, 'Qrcode');
    } else { // chrome
      const blob = new Blob([blobData], { type: 'image/png' });
      // tslint:disable-next-line: no-shadowed-variable
      const url = window.URL.createObjectURL(blob);
      // window.open(url);
      const link = document.createElement('a');
      link.href = url;
      link.download = 'Qrcode';
      link.click();
    }

    if (window.navigator.msSaveOrOpenBlob) { // IE10+
      window.navigator.msSaveOrOpenBlob(blobData, 'Qrcode');
    }
    else {
      const blob = new Blob([blobData], { type: 'image/png' });
      // tslint:disable-next-line: prefer-const
      let url = window.URL.createObjectURL(blob);
      window.open(url);
    }
  }

  // tslint:disable-next-line: typedef
  private convertBase64ToBlob(Base64Image: any) {
    // SPLIT INTO TWO PARTS
    const parts = Base64Image.split(';base64,');
    // HOLD THE CONTENT TYPE
    const imageType = parts[0].split(':')[1];
    // DECODE BASE64 STRING
    const decodedData = window.atob(parts[1]);
    // CREATE UNIT8ARRAY OF SIZE SAME AS ROW DATA LENGTH
    const uInt8Array = new Uint8Array(decodedData.length);
    // INSERT ALL CHARACTER CODE INTO UINT8ARRAY
    for (let i = 0; i < decodedData.length; ++i) {
      uInt8Array[i] = decodedData.charCodeAt(i);
    }
    // RETURN BLOB IMAGE AFTER CONVERSION
    return new Blob([uInt8Array], { type: imageType });
  }
  // tslint:disable-next-line: typedef
}

